		baoadd(){
			if(this.GET_MY.ress.addrName==''){
				uni.showToast({
					title: '姓名不能为空',
					icon: 'none',
					duration: 1000
				});
				return false;
			}else if(this.GET_MY.ress.addrPhone==''){
				uni.showToast({
					title: '手机号不能为空',
					icon: 'none',
					duration: 1000
				});
				return false;
			}else if(!(/^1[34578]\d{9}$/.test(this.GET_MY.ress.addrPhone))){
				uni.showToast({
					title: '手机号格式有误',
					icon: 'none',
					duration: 1000
				});
				return false;
			}else if(this.arr==''&&this.GET_MY.ress.addrPcodeStr==''){
				uni.showToast({
					title: '请选择所在地区',
					icon: 'none',
					duration: 1000
				});
				return false;
			}else if(this.GET_MY.ress.address==''){
				uni.showToast({
					title: '请输入详细地址',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {
					//手机号
					"addrPhone":this.GET_MY.ress.addrPhone,
					//名称
					"addrName":this.GET_MY.ress.addrName,
					//省市区地址
					"addrPcode":this.arr.pcode,
					"addrCcode":this.arr.ccode,
					"addrAcode":this.arr.tcode,
					"address":this.addres,
					//是否默认地址
					"addrState":this.GET_MY.ress.addrState,
					//用户ID
					"userId":landRegistLG.user.id,
					//传递就编辑 不传递就插入
					"id":this.id,
				}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/address/', //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.address();
						uni.navigateBack({})
						// console.log(this.list)
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		address(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/address?userId='+landRegistLG.user.id, //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content
						// console.log(this.list)
						this.$store.commit('setaddress', this.list); // 更新vuex
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		xuan(e){//改变默认地址的按钮
			console.log(e)
			if(e===1){
				this.num='0';
				this.GET_MY.ress.addrState = '0';
			}else if(e===0){
				this.num='1';
				this.GET_MY.ress.addrState = '1';
			}
		}
